*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  font-family: inherit;
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
